/* vollkorn-400normal - latin */
@font-face {
  font-family: 'Vollkorn';
  font-style: normal;
  font-display: swap;
  font-weight: 400;
  src:
    local('Vollkorn Regular '),
    local('Vollkorn-Regular'),
    url('./files/vollkorn-latin-400.woff2') format('woff2'), /* Super Modern Browsers */
    url('./files/vollkorn-latin-400.woff') format('woff'); /* Modern Browsers */
}

/* vollkorn-400italic - latin */
@font-face {
  font-family: 'Vollkorn';
  font-style: italic;
  font-display: swap;
  font-weight: 400;
  src:
    local('Vollkorn Regular italic'),
    local('Vollkorn-Regularitalic'),
    url('./files/vollkorn-latin-400italic.woff2') format('woff2'), /* Super Modern Browsers */
    url('./files/vollkorn-latin-400italic.woff') format('woff'); /* Modern Browsers */
}

